import {Component} from 'react';
import {ParsedKLE, parseKLE} from './kle-parser';
import {parseKLERaw} from './kle-parser';
import {parseLayout} from './layout-parser';
import KEYBOARDS from '../keyboards';
import stringify from 'json-stringify-pretty-compact';
import {existsSync, mkdirSync, writeFileSync} from 'fs';
import {join} from 'path';
import {Device} from '../types';

export const enum LightingSupport {
  None,
  QMKLighting,
  WTRGBBacklight,
  WTMonoBacklight
}

export type CustomMenu = {title: string; component: typeof Component};

export type DeviceMeta = {
  name: string;
  layout: string;
  matrixLayout: string;
  lights: LightingSupport;
  overrideMatrixIndexing?: boolean;
  customMenus?: CustomMenu[];
  vendorProductId: number;
};

export type ParsedMatrix = any;
export type CompiledDeviceMeta = DeviceMeta & {
  compiledLayout: ParsedKLE;
  compiledMatrixLayout: any;
};

export type DeviceMetaMap = {[key: number]: DeviceMeta};
export type CompiledDeviceMetaMap = {[key: number]: CompiledDeviceMeta};

export const DEVICE_META_MAP: DeviceMetaMap = KEYBOARDS.reduce(
  (accum, deviceMeta) => ({
    ...accum,
    [deviceMeta.vendorProductId]: deviceMeta
  }),
  {}
);

const COMPILED_DEVICE_META_MAP: CompiledDeviceMetaMap = Object.entries(
  DEVICE_META_MAP
).reduce(
  (p, [k, v]: [string, any]) => ({
    ...p,
    [k]: {
      ...v,
      compiledLayout: parseKLERaw(v.layout),
      compiledMatrixLayout: parseLayout(v.matrixLayout)
    }
  }),
  {}
);

function generateEncodedLayout(layout: string, matrixLayout: string) {
  const pins = [...parseLayout(matrixLayout).layout]
    .reverse()
    .map(({col, row}) => `${row},${col}`);
  const encodedLayout = parseKLE(layout).map(row =>
    row.map(elem => (typeof elem === 'string' ? pins.pop() : elem))
  );
  return encodedLayout;
}

function generateDeviceJSON(v: any) {
  const {rows, cols} = parseLayout(v.matrixLayout);
  return {
    name: v.name,
    vendorId: `0x${v.vendorProductId.toString(16).slice(0, 4)}`,
    productId: `0x${v.vendorProductId.toString(16).slice(4)}`,
    lights: v.lights,
    matrix: {rows, cols},
    layouts: {all: generateEncodedLayout(v.layout, v.matrixLayout)}
  };
}

export function writeAllDevices() {
  const dir = 'dist';
  if (!existsSync(dir)) {
    mkdirSync(dir);
  }

  Object.entries(DEVICE_META_MAP).forEach(([k, v]: [string, any]) =>
    writeFileSync(
      join('.', dir, `${v.name.toLowerCase().replace(/[\s-]/gi, '_')}.json`),
      stringify(generateDeviceJSON(v))
    )
  );
}

export function getKeyboardFromDevice(device: Device): CompiledDeviceMeta {
  return COMPILED_DEVICE_META_MAP[getKeyboardId(device)];
}

export function getKeyboardFromVendorProductId(id: number): CompiledDeviceMeta {
  return COMPILED_DEVICE_META_MAP[id];
}

export function getKeyboardId({productId, vendorId}) {
  return vendorId * 65536 + productId;
}

export function getLayoutFromDevice(device: Device) {
  const kb = getKeyboardFromDevice(device);
  return kb.compiledLayout;
}

export function getMatrixLayoutFromDevice(device: Device) {
  const kb = getKeyboardFromDevice(device);
  return kb.compiledMatrixLayout;
}
