import * as React from 'react';
const styles = require('./title-bar.css');
import {connect} from 'react-redux';
import {CompiledDeviceMeta} from 'app/utils/device-meta';
import {RootState} from '../redux';
import {getSelectedDefinition} from '../redux/modules/keymap';
import {VIADefinitionV2, LightingTypeDefinition} from 'via-reader/dist/types';

export const Title = {
  KEYS: 'Keys',
  LIGHTING: 'Lighting',
  SAVE_LOAD: 'Save/Load',
  DEBUG: 'Debug'
};

type ReduxState = {
  selectedDefinition: VIADefinitionV2;
};

type Props = {
  meta: CompiledDeviceMeta;
  selectedTitle: string;
  setSelectedTitle: (title: string) => void;
};

class DisconnectedTitleBar extends React.Component<Props & ReduxState> {
  get isDetected() {
    return !!this.props.selectedDefinition;
  }

  get keyboardName() {
    const definition = this.props.selectedDefinition;
    return (definition && definition.name) || 'N/A';
  }

  getTitlesForKeyboard(keyboard) {
    const {lighting} = this.props.selectedDefinition;
    let titles = [Title.KEYS];
    if (lighting === LightingTypeDefinition.WTRGBBacklight) {
      titles = [...titles, Title.LIGHTING];
    }
    if (keyboard.customMenus) {
      titles = [...titles, ...keyboard.customMenus.map(menu => menu.title)];
    }
    titles = [...titles, Title.SAVE_LOAD];
    if (
      process.env.NODE_ENV === 'development' ||
      process.env.DEBUG_PROD === 'true'
    ) {
      titles = [...titles, Title.DEBUG];
    }
    return titles;
  }

  render() {
    const {selectedTitle} = this.props;

    return (
      <div className={styles.titleBarContainer}>
        <div className={styles.titles}>
          {this.isDetected ? (
            [
              <div key="kbname" className={styles.kbName}>
                {this.keyboardName}
              </div>,
              this.getTitlesForKeyboard(this.props.meta).map(title => (
                <div
                  onClick={_ => this.props.setSelectedTitle(title)}
                  key={title}
                  className={[
                    title === selectedTitle && styles.selected,
                    styles.title
                  ].join(' ')}
                >
                  {title}
                </div>
              ))
            ]
          ) : (
            <div className={[styles.kbName, styles.kbDisconnected].join(' ')}>
              No kb detected
            </div>
          )}
        </div>
      </div>
    );
  }
}
const mapStateToProps = (state: RootState) => ({
  selectedDefinition: getSelectedDefinition(state.keymap)
});
export const TitleBar = connect(mapStateToProps)(DisconnectedTitleBar);
