import * as React from 'react';
import {render} from 'react-dom';
import electron from 'electron';
import log from 'electron-log';
import Root from './containers/Root';
import {configureStore, history} from './redux/configureStore';
import {actions, reloadConnectedDevices} from './redux/modules/keymap';
import './app.global.css';

console.error = log.error;
log.catchErrors({showDialog: false});
const store = configureStore();

electron.ipcRenderer.on('import-definition', (_: any, message: any) => {
  store.dispatch(actions.loadDefinition(message));
  store.dispatch(reloadConnectedDevices() as any);
});

render(
  <Root store={store} history={history} />,
  document.getElementById('root')
);
